        .tarjeta.shadow.bg-white.p-3(x="20%" y="25%" numero="+")
          .h5.mb-2 Identidad de género
          p La galleta de jengibre sitúa la identidad de género en la cabeza. Es la percepción subjetiva que una persona tiene sobre sí mismo en cuanto a su propio género. Se identifica (o no) con los roles de hombre o mujer.
