      p.mb-5(data-aos='fade-right') Antes de abordar las directrices de la presente política pública, es crucial comprender el significado del acrónimo LGBTIQ+, que se define como una categoría identitaria y política. Esta categoría engloba diversas orientaciones sexuales, identidades de género y diferentes estados de intersexualidad. El acrónimo LGBTIQ+ se centra en la reivindicación y exigibilidad de los derechos en el ámbito de las luchas sexuales y de género:
